export function rewriteCss(code: string, origin: string): string {
  return code.replace(/url\(\s*(['"]?)(.*?)\1\s*\)/gi, (match, quote, url) => {
    const trimmedUrl = url.trim();
    if (!trimmedUrl) return match;

    const rewritten = self.__envade$rewrite.url.rewriteUrl(trimmedUrl, origin);

    if (rewritten !== url) {
      const q = quote || '';
      return `url(${q}${rewritten}${q})`;
    }

    return match;
  });
}
