export function rewriteSrc(srcset: string, origin: URL): string {
  return srcset
    .split(',')
    .map((set) =>
      set
        .trim()
        .split(/\s+/)
        .map((part, index) =>
          index === 0 ? self.__envade$rewrite.url.rewriteUrl(part, origin) : part,
        )
        .join(' '),
    )
    .join(', ');
}
