const tobeDeleted = new Set([
  'cross-origin-embedder-policy',
  'cross-origin-opener-policy',
  'cross-origin-resource-policy',
  'content-security-policy',
  'content-security-policy-report-only',
  'expect-ct',
  'feature-policy',
  'origin-isolation',
  'strict-transport-security',
  'upgrade-insecure-requests',
  'x-content-type-options',
  'x-download-options',
  'x-frame-options',
  'x-permitted-cross-domain-policies',
  'x-powered-by',
  'x-xss-protection',
  'clear-site-data',
]);

const directRewrites = new Set(['location', 'content-location', 'referer']);

export async function rewriteHeaders(headers: Headers, origin: string) {
  const newHeaders = new Headers();

  headers.forEach((value, key) => {
    newHeaders.set(key.toLowerCase(), value);
  });

  for (const header of tobeDeleted) {
    if (newHeaders.has(header)) {
      newHeaders.delete(header);
    }
  }

  for (const header of ['referer', 'location', 'content-location']) {
    const value = newHeaders.get(header);
    if (value) {
      const rewritten = self.__envade$rewrite.url.rewriteUrl(value, origin);
      newHeaders.set(header, rewritten);
    }
  }

  for (const header of directRewrites) {
    if (newHeaders.has(header)) {
      const raw = newHeaders.get(header);
      if (raw) {
        const rewritten = self.__envade$rewrite.url.rewriteUrl(raw, origin);
        try {
          const parsed = new URL(rewritten);
          const final = parsed.toString();
          newHeaders.set(header, final);
        } catch {}
      }
    }
  }

  if (newHeaders.has('link')) {
    const raw = newHeaders.get('link');
    if (raw) {
      const rewritten = raw.replace(/<([^>]+)>/g, (_m, uri) => {
        const newUri = self.__envade$rewrite.url.rewriteUrl(uri, origin);
        return `<${newUri}>`;
      });
      newHeaders.set('link', rewritten);
    }
  }

  return newHeaders;
}
