import { generate } from 'astring';
import { walk } from 'estree-walker';
import type { Node } from 'estree';
import { parseModule } from 'meriyah';

export function rewriteScript(content: string, origin: URL): string {
  try {
    if (typeof content !== 'string') content = String(content);
    const ast = parseModule(content, { module: true, webcompat: true });

    walk(ast as Node, {
      enter(node, parent, prop, index) {
        if (
          (node.type === 'MemberExpression' &&
            node.object.type === 'Identifier' &&
            ['window', 'self', 'document', 'globalThis'].includes(node.object.name) &&
            (
              (!node.computed && node.property.type === 'Identifier' && node.property.name === 'location') ||
              (node.computed && node.property.type === 'Literal' && node.property.value === 'location')
            )) ||
          (node.type === 'Identifier' && node.name === 'location')
        ) {
          if (parent && prop !== undefined) {
            const newNode: Node = {
              type: 'Identifier',
              name: 'scope$location',
            };
            if (Array.isArray(parent[prop]) && typeof index === 'number') {
              parent[prop][index] = newNode;
            } else {
              parent[prop] = newNode;
            }
            this.skip();
            return;
          }
        }

        if (
          (node.type === 'ImportDeclaration' ||
            node.type === 'ExportNamedDeclaration' ||
            node.type === 'ExportAllDeclaration') &&
          node.source?.type === 'Literal'
        ) {
          if (parent && prop !== undefined) {
            const encoded = (self as any).__envade$rewrite.url.rewriteUrl(
              String(node.source.value),
              origin,
            );
            const newNode: Node = {
              ...node,
              source: { ...node.source, value: encoded },
            };
            if (Array.isArray(parent[prop]) && typeof index === 'number') {
              parent[prop][index] = newNode;
            } else {
              parent[prop] = newNode;
            }
            this.skip();
            return;
          }
        }

        if (node.type === 'ImportExpression' && node.source?.type === 'Literal') {
          if (parent && prop !== undefined) {
            const newNode: Node = {
              ...node,
              source: {
                ...node.source,
                value: (self as any).__envade$rewrite.url.rewriteUrl(
                  String(node.source.value),
                  origin,
                ),
              },
            };
            if (Array.isArray(parent[prop]) && typeof index === 'number') {
              parent[prop][index] = newNode;
            } else {
              parent[prop] = newNode;
            }
            this.skip();
            return;
          }
        }

        if (
          node.type === 'CallExpression' &&
          node.callee.type === 'Identifier' &&
          node.callee.name === 'importScripts'
        ) {
          if (parent && prop !== undefined) {
            const newNode: Node = {
              ...node,
              arguments: node.arguments.map((arg) => {
                if (arg.type === 'Literal') {
                  return {
                    ...arg,
                    value: (self as any).__envade$rewrite.url.rewriteUrl(String(arg.value), origin),
                  };
                }
                return arg;
              }),
            };
            if (Array.isArray(parent[prop]) && typeof index === 'number') {
              parent[prop][index] = newNode;
            } else {
              parent[prop] = newNode;
            }
            this.skip();
            return;
          }
        }

        if (
          node.type === 'MemberExpression' &&
          node.property.type === 'Identifier' &&
          node.property.name === 'integrity'
        ) {
          if (parent && prop !== undefined) {
            const newNode: Node = {
              type: 'Identifier',
              name: 'undefined',
            };
            if (Array.isArray(parent[prop]) && typeof index === 'number') {
              parent[prop][index] = newNode;
            } else {
              parent[prop] = newNode;
            }
            this.skip();
            return;
          }
        }
      },
    });

    return generate(ast);
  } catch (err: any) {
    console.warn('[JS REWRITER] Failed to rewrite JS:', err, err.message);
    return content;
  }
}
