import { render } from 'dom-serializer';
import DomHandler, { Element } from 'domhandler';
import { hasAttrib } from 'domutils';
import { ElementType, Parser } from 'htmlparser2';

const cspAttrs = ['nonce', 'csp'];
const removeAttrs = ['integrity'];
const urlAttrs = [
  'href',
  'src',
  'ping',
  'profile',
  'movie',
  'poster',
  'background',
  'data',
  'manifest',
  'icon',
  'longdesc',
];
const htmlAttrs = ['srcdoc'];
const cssAttrs = ['style', 'media'];
const srcsetAttrs = ['srcset'];

const jsEventAttrs = [
  'onclick',
  'ondblclick',
  'onmousedown',
  'onmouseenter',
  'onmouseleave',
  'onmousemove',
  'onmouseout',
  'onmouseover',
  'onmouseup',
  'onmousewheel',
  'onwheel',
  'onkeydown',
  'onkeypress',
  'onkeyup',
  'onchange',
  'oninput',
  'oninvalid',
  'onreset',
  'onsubmit',
  'onload',
  'onunload',
  'onresize',
  'onscroll',
  'onerror',
  'onabort',
  'ondrag',
  'ondragend',
  'ondragenter',
  'ondragexit',
  'ondragleave',
  'ondragover',
  'ondragstart',
  'ondrop',
  'oncanplay',
  'oncanplaythrough',
  'ondurationchange',
  'onemptied',
  'onended',
  'onloadeddata',
  'onloadedmetadata',
  'onloadstart',
  'onpause',
  'onplay',
  'onplaying',
  'onprogress',
  'onratechange',
  'onseeked',
  'onseeking',
  'onstalled',
  'onsuspend',
  'ontimeupdate',
  'onvolumechange',
  'onwaiting',
  'onfocus',
  'onblur',
  'oncontextmenu',
  'onshow',
  'ontoggle',
  'onselect',
  'onclose',
  'oncancel',
  'oncuechange',
  'onsort',
  'oncopy',
  'oncut',
  'onpaste',
  'onselectstart',
  'onanimationstart',
  'onanimationend',
  'onanimationiteration',
  'ontransitionstart',
  'ontransitionend',
  'ontransitionrun',
  'ontransitioncancel',
];

const formAttrsMap: Record<string, string[]> = {
  action: ['form'],
  formaction: ['button', 'input', 'textarea', 'submit'],
};

const cspTags = [
  'content-security-policy',
  'content-security-policy-report-only',
  'x-content-security-policy',
];

export async function rewriteHtml(content: string, origin: URL) {
  const dom = new DomHandler();
  const parser = new Parser(dom);
  parser.write(content);
  parser.end();
  return render(rewriteElement(dom.root as unknown as Element, origin));
}

function rewriteElement(element: Element, origin: URL) {
  element.children = element.children.filter((child) => {
    if (child.type === ElementType.Tag && child.name === 'meta' && hasAttrib(child, 'http-equiv')) {
      const httpEquiv = child.attribs['http-equiv'].toLowerCase();
      if (cspTags.includes(httpEquiv)) return false;
    }
    if (child.type === ElementType.Tag) {
      rewriteElement(child, origin);
    }
    return true;
  });

  for (const child of element.children) {
    if (child.type === ElementType.Script || child.type === ElementType.Style) {
      rewriteElement(child, origin);
      const dataNode = child.children[0];
      if (dataNode && 'data' in dataNode) {
        dataNode.data =
          child.type === ElementType.Style
            ? self.__envade$rewrite.css(dataNode.data, origin)
            : self.__envade$rewrite.javascript(dataNode.data, origin);
      }
    }
  }

  [...cspAttrs, ...removeAttrs].forEach((attr) => {
    if (hasAttrib(element, attr)) {
      delete element.attribs[attr];
    }
  });

  for (const attr of cssAttrs) {
    if (hasAttrib(element, attr)) {
      element.attribs[attr] = self.__envade$rewrite.css(element.attribs[attr], origin);
    }
  }

  for (const attr of htmlAttrs) {
    if (hasAttrib(element, attr)) {
      element.attribs[attr] = self.__envade$rewrite.html(element.attribs[attr], origin);
    }
  }

  for (const attr of urlAttrs) {
    if (hasAttrib(element, attr)) {
      element.attribs[attr] = self.__envade$rewrite.url.rewriteUrl(element.attribs[attr], origin);
    }
  }

  for (const [attr] of Object.entries(formAttrsMap)) {
    if (hasAttrib(element, attr)) {
     element.attribs[attr] = self.__envade$rewrite.url.rewriteUrl(element.attribs[attr], origin);
    }
  }

  for (const attr of srcsetAttrs) {
    if (hasAttrib(element, attr)) {
      element.attribs[attr] = self.__envade$rewrite.srcset(element.attribs[attr], origin);
    }
  }

  for (const attr of jsEventAttrs) {
    if (hasAttrib(element, attr)) {
      element.attribs[attr] = self.__envade$rewrite.javascript(element.attribs[attr], origin);
    }
  }

if (element.name === 'html') {
  let head = element.children.find(
    (el) => el.type === ElementType.Tag && el.name === 'head'
  ) as Element | undefined;

  if (!head) {
    head = new Element('head', {}, []);
    element.children.unshift(head);
  }

  const stp = ['client', 'bundle', 'config'];
  for (const script of stp) {
    head.children.unshift(
      new Element('script', {
        src: location.origin + self.__envade$config.files[script],
      })
    );
  }
}


if (['a', 'span'].includes(element.name) && hasAttrib(element, 'target')) {
  const target = element.attribs['target'].toLowerCase();
  if (target !== '_self') {
    element.attribs['target'] = '_self';
  }
}

  return element;
}
